import axios from 'axios';
import cityActions from '../redux/city/actions';

const URL =  'https://api.openweathermap.org/data/2.5/weather?q=';
const KEY = '&appid=78a5f0c7f62a233924362c6c4d6ac0f9';

const {
  actionAddCity,
  actionAddError
} = cityActions;

export const getCityWeatherSimply = (cityName, data) => {
  axios.get(URL+cityName+KEY)
  .then(response => {
    return data(response.data)
  })
  .catch(error => console.log(error))
}

export const getCityWeather = (cityName, data, cityApiVault,formik) => {
  axios.get(URL+cityName+KEY)
  .then(response => {
      return data(response.data)
  }) 
  .catch(error => console.log(error));
}

export const addCityweather = (dispatch, cityName, formik, cityApiVault) => {
  axios.get(URL+cityName+KEY)
  .then(response => {
    let filterCity = cityApiVault.filter(item => item === response.data.name);

    console.log(filterCity);

    if (filterCity.length === 0) {
      return dispatch(actionAddCity(cityName));
    }else {
      return formik.setFieldError('city', 'Такой город уже есть!')
    }

    
  })
  .catch(error => {
    formik.setFieldError('city', 'Город не найден!')
    //dispatch(actionAddError('Город не найден!'))
  })
}