import React, { Component , useRef, useState, useContext, useEffect, useMemo, useCallback} from 'react';
import {useSelectore, useDispatch} from 'react-redux';
import * as Yup from 'yup';
import {useFormik} from 'formik';
//components
import {
  Cities,
  Weather,
  Informer,
  Preloader
} from './components';
import {useSelector} from 'react-redux';
import {
  Typography
} from '@material-ui/core';
import './App.css';

//react-router
import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink,
} from "react-router-dom";

import actionsCity from './redux/city/actions';
import {
  addCityweather
} from './api';

//images
import Logo from './materials/logo.png';
//uuid 
import {v4 as uuidv4} from 'uuid';
import { AlertProvider, AlertContext, AlertContextToggle } from './components/Alert/AlertContext';


const App = () =>  {
  const dispatch = useDispatch();
  const cityVault = useSelector(state => state.weater.cityVault);
  const cityApiVault = useSelector(state => state.weater.cityApiVault);

  const {
    actionAddCity
  } = actionsCity;

  const onHandleClick = (e) => {
    addCityweather(dispatch, formik.values.city, formik,cityApiVault);

    formik.handleReset();

  }

  const checkSameName = (name) => {
    let reuseName = name || '';
    const filterCity = cityVault.filter(item => item.toLowerCase() === reuseName.toLowerCase());

    if (filterCity.length !== 0) {
      return false;
    }

    return true;
  }
  const validationSchema = Yup.object({
    city: Yup
          .string()
          .required('Это поле обязательное!')
          .min(2, 'Минимальное кол-во символов 2!')
          .max(50, 'Максимальное кол-во символов 50!')
          .matches(/[А-я]/ , 'Допускается только кириллица!')
          .test('Такой город уже есть!', "Такой город уже есть!", value => checkSameName(value))
  })
 

  const formik = useFormik({
    initialValues: {
      city: '',
    },
    onHandleClick,
    validationSchema
  })



  return (
    <AlertProvider>
      <div className="App">
        <div className="container__max">
        <Router>
          <div className="container__header" id="logo">
            <NavLink className="logo__header" to="/"><img src={Logo} alt="Погода"/>
              <Typography variant="h4">Погода</Typography>
            </NavLink>
          </div>
          <div className="container">
              <Switch>
                <Route path="/">
                  {cityVault ? cityVault.map((item,index) => <Weather formik={formik} cityName={item} key={index} /> ) : "loading"}
                </Route>
              </Switch>
              <Cities handleClick={onHandleClick} formik={formik}
              />
          </div>
        </Router> 
        </div>
      </div>
    </AlertProvider>
  );
}
 
export default App;
