const actions  = {
    ADD_CITY: "ADD_CITY",
    ADD_ERRORS: "ADD_ERRORS",
    GET_CITY: "GET_CITY",

    actionAddCity(city) {
        return {type: actions.ADD_CITY, payload: city}
    },
    actionAddError(error) {
        return {type: actions.ADD_ERRORS, payload: error}
    },
    actionGetCity(name) {
        return {type: actions.GET_CITY, payload: name}
    }

}

export default actions;