
import actionsCity from './actions';

const initialState = {
    cityVault: [
        'Казань',
        "Москва"
    ],
    cityApiVault: [

    ],
    errorsVault: ''
}

const {
    ADD_CITY,
    ADD_ERRORS,
    GET_CITY
} = actionsCity;


export const cityStore = (
    state = initialState,
    {type, payload}
) => {
    switch (type) {
        case ADD_CITY: {
            return {
                ...state,
                cityVault: [payload,...state.cityVault],
            }
        }
        case GET_CITY: {
            return {
                ...state,
                cityApiVault: state.cityApiVault.concat(payload)
            }
        }
        case ADD_ERRORS: {
            return {
                ...state,
                errorsVault: payload
            }
        }
        default:
            return {
                ...state
            }
    }
}