import React, { useEffect , useCallback, useState, useMemo } from 'react';
import {useDispatch, useSelector} from 'react-redux';
import actionsCity from '../../redux/city/actions';
import './style.scss';
import {
    Typography
} from '@material-ui/core'

//images
import temp from '../../materials/gradusi.png';
import wind from '../../materials/wind.png';
import weter from '../../materials/weter.png';
import city2 from '../../materials/city2.png';
import {
    getCityWeather,
    getCityWeatherSimply
} from '../../api/';

const Weather = ({
    cityName,
    formik
}) => {
    const dispatch = useDispatch();

    const cityApiVault = useSelector(state => state.weater.cityApiVault);

    const {
        actionGetCity
    } = actionsCity;

    const [wether, setWehter] = useState({});

    const isObject = (obj) => {
        for (let key in obj) {
            return true;
        }
        return false;
    }

    useEffect(() => {
        getCityWeather(cityName, setWehter, cityApiVault, formik);
        
        setInterval(() => {
            getCityWeatherSimply(cityName,setWehter)
        }, 30000)
        
    }, [cityName])

    useEffect(() => {
        if (wether.name ){
            dispatch(actionGetCity(wether.name))
        }
    }, [wether])


    // useMemo(() => {
    //     setInterval(() => {
    //         getCityWeather(cityName,setWehter)
    //     }, 10000)
    // }, [])
    return (

        <div className="container__weather">
            <ul>
                <li>
                    <img src={city2} alt="Город"/>
                    <Typography variant="h5">{cityName}</Typography>
                </li>
                <li>
                    <img src={temp} alt="Температура"/>
                    <Typography variant="h5">{isObject(wether) ? <span>{Math.ceil(-273.15 + wether.main.temp)}&#8451; | {Math.ceil(wether.main.temp * 9/5 - 459.67)}&#8457;</span>  : 'loading'}</Typography>
                </li>
                <li>
                    <img src={weter} alt="Влажность"/>
                    <Typography variant="h5">
                        {isObject(wether) ? wether.main.humidity+'%' : 'loading'}
                    </Typography>
                </li>
                <li>
                    <img src={wind} alt="Ветер"/>
                    <Typography variant="h5">{isObject(wether) ? wether.wind.speed+' м/сек' : 'loading'}</Typography>
                </li>
            </ul>
        </div>         
    )
}
 
export default Weather;