import './style.css';

//material UI
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import {makeStyles} from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import {useSelector} from 'react-redux';






import {AlertError} from '../../components'
import { useState, useEffect ,useContext} from 'react';
import { AlertContext } from '../Alert/AlertContext';

const userStyles = makeStyles((theme) => ({
    modal: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        ouline: "none",
    },
    paper: {
        width: '350px',
        backgroundColor: theme.palette.background.paper,
        boxShadow: theme.shadows[5],
        padding: theme.spacing(4,4,3),
        outline: 'none'
    },
    inputWidth: {
        width: '100%'
    }
}));

const Cities = ({
    inputValue,
    formik,
    handleClick
}) => {

    const errorsVault = useSelector(state => state.weater.errorsVault);

    const classes = userStyles();
    const [modalOpen,setModalOpen] = useState(false);


    function handleModal() {
        setModalOpen(prev => !prev)
    }

    useEffect(() => {
        let button = document.querySelector('.addBtn');

        button.addEventListener('click', handleModal)

        return () => {
            button.removeEventListener('click', handleModal)
        }
    }, [modalOpen])



    return (
        <div className="cities">
            <Button className="addBtn"  variant="outlined" color="primary" alt="Click to add City">Добавить город</Button>
            <Button className="addBtn" variant="outlined" color="secondary" alt="Click to remove Cites">Очистить список</Button>
            <div className="modal__container">
                <Modal
                    aria-labelledby="transition-modal-title"
                    aria-describedby="transition-modal-description"
                    className={classes.modal}
                    open={modalOpen}
                    onClose={handleModal}
                    closeAfterTransition
                    BackdropComponent={Backdrop}
                    BackdropProps={{
                        timeout: 500,
                    }}
                >
                    <Fade in={modalOpen}>
                        <div className={classes.paper}>
                            <div>
                                <TextField 
                                    {...formik.getFieldProps('city')}
                                    className={classes.inputWidth}
                                    id="outlined-basic" 
                                    width="500" 
                                    label="Город"
                                    placeholder="Введите город который хотите добавить" 
                                    variant="outlined" 
                                    defaultValue=""  
                                    ref={inputValue} 
                                />
                            </div>
                            {formik.errors.city ? <AlertError message={formik.errors.city} setError={true} /> : null}
                            {errorsVault ? <AlertError message={errorsVault} setError={true} /> : null}
                            <div className="buttonsAdd">
                                <Button onClick={handleClick}  variant="outlined" disabled={!(formik.dirty && formik.isValid)} color="primary">Добавить</Button>
                                <Button onClick={handleModal} variant="outlined" color="secondary">Закрыть</Button>
                            </div>
                        </div>
                    </Fade>
                </Modal>
            </div>
        </div>
    );
}
 
export default Cities;